@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  /* color: rgb(12, 12, 13); */
}
body{
  background-color: #282c34;
}
.App {
  max-width: 600px;
  min-height: 100vh;
  text-align: center;
  display: grid;
  grid-template-columns: 100px 1fr;
  font-size: 18px;
  color: white;
  /* padding: 20px; */
  margin: 0 auto;
  border-left: 1px solid rgba(97, 218, 251, 0.1);
  border-right: 1px solid rgba(97, 218, 251, 0.1);
}
.taskBlock{
  margin-top: 20px;
}
.emojiTicker{
  font-size: 20px;
  width: 30px;
  display: inline-block;
  text-align: center;

}

.buttonTask{
  margin-top: 5px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}
.CategorySmile{
  width: 100px;
  height: 50px;
  font-size: 15px;
  /* border: 2px solid rgb(97, 218, 251); */
  display: flex;
  align-items: center;
  justify-content: center;
}
.CategorySmile h1{
  user-select: none;
  cursor: pointer;
}
.CategorySmile h1:hover{
  opacity: 0.5;
}
.classesBlock{
  border-right: 1px solid rgb(97, 218, 251, 0.1);
  /* height: 100vh; */
  overflow-x: hidden;
}
.classesBlock::-webkit-scrollbar {
  width: 0;
}
.CategorySmile a{
  text-decoration: none;
}